/// #string-to-lowercase($input-string) => string
/// Returns the calling string value converted to lowercase
/// Alias for to-lower-case String SASS built-in function
/// @param {string} $input-string
/// @return {string}
/// @example
///   @debug string-to-lowercase('Hello World');
///   // => "hello world"

@function string-to-lowercase($input-string) {
    @return to-lower-case($input-string);
}
